import React, { ComponentProps, ReactNode } from 'react';
import { Button, makeStyles, Theme } from '@material-ui/core';
import CountDown from 'components/Utils/CountDown';
import { useCountDown } from 'src/hooks/useCountDown';

const useStyles = makeStyles<Theme>(theme => ({
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  button: {
    marginBottom: theme.spacing(2),
  },
}));

interface Props extends ComponentProps<typeof Button> {
  children?: ReactNode;
  expiry: Date;
}
const MentoringApplyButton = ({ expiry, className, style, children = '  ', ...rest }: Props) => {
  const styles = useStyles();
  const { expired } = useCountDown(expiry);
  const buttonText = expired ? '' : children;

  return (
    <span className={styles.wrapper} style={style}>
      <Button disabled={expired} className={`${styles.button} ${className}`} {...rest}>
        {buttonText}
      </Button>
      {!expired && <CountDown expiry={expiry} />}
    </span>
  );
};

export default MentoringApplyButton;
