import React, { ComponentProps } from 'react';
import { Button, makeStyles, Theme } from '@material-ui/core';
import Emoji from 'components/Utils/Emoji';
import { useCountDown } from 'src/hooks/useCountDown';

const useStyle = makeStyles<Theme>(theme => ({
  description: {
    fontSize: theme.typography.body2.fontSize,
    '& strong': {
      fontSize: theme.typography.subtitle1.fontSize,
      marginRight: 2,
      color: theme.palette.primary.main,
    },
  },
}));

interface Props extends ComponentProps<typeof Button> {
  expiry: Date;
  expiredText?: string;
}
const CountDown = ({ expiry, expiredText = '' }: Props) => {
  const styles = useStyle();
  const { count, expired } = useCountDown(expiry);

  return (
    <div className={styles.root}>
      {expired ? (
        expiredText
      ) : (
        <span className={styles.description}>
          <strong>{count.days}</strong> <strong>{count.hours}</strong> <strong>{count.minutes}</strong>{' '}
          <strong>{count.seconds}</strong>   <Emoji emoji="" name="thanks" />
        </span>
      )}
    </div>
  );
};

export default CountDown;
